.impact__points-container {
    display: flex;
    max-width: 1360px;
    justify-content: space-between;
    gap: 40px;
    margin: 120px 20px 0 20px;
}

@media screen and (max-width: 680px) {
    .impact__points-container {
        flex-direction: column;
        gap: 120px;
    }
}
