.contact-form__container {
    background-color: white;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    padding: 60px 80px 40px 80px;
    position: relative;
    max-width: 600px;
    width: 100%;
}

@media screen and (max-width: 1154px) {
    .contact-form__container {
        max-height: 88vh;
        overflow: auto;
    }
}
